.team-page {
    padding-bottom: 50px;
}

.team-header {
    padding: 5%;
}

.testimonial-block-vertical {
    padding: 2%;
}

.testimonial-block-vertical-quote {
    background: rgba(23, 121, 186, 0.25);
    padding: 1rem;
    border-radius: 10px;
    margin-bottom: 1.875rem;
    position: relative;
}

.testimonial-block-vertical-quote p {
    margin-bottom: 0;
}

.testimonial-block-vertical-quote::after {
    content: '';
    bottom: -1.25rem;
    left: 2.25rem;
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 1.25rem 1.125rem 0 1.125rem;
    border-color: rgba(23, 121, 186, 0.25) transparent transparent transparent;
    position: absolute;
}

.testimonial-block-vertical-person {
    display: -ms-flexbox;
    display: flex;
    margin-left: 1.65rem;
    -ms-flex-align: center;
    align-items: center;
}

.testimonial-block-vertical-avatar {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.testimonial-block-vertical-name {
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 0;
    margin-left: 0.65rem;
}

.testimonial-block-vertical-info {
    text-transform: uppercase;
    font-size: 14px;
    color: #8a8a8a;
    margin-bottom: 0;
    margin-left: 0.65rem;
}


